/* Loading.css */
.loadingContainer {
    display        : flex;
    justify-content: center;
    align-items    : center;
    padding        : 10px;
    /* height      : 100vh; */
}

.loadingSpinner {
    border       : 10px solid #f3f3f3;
    border-top   : 16px solid #3498db;
    border-radius: 50%;
    width        : 60px;
    height       : 60px;
    animation    : spin 0.5s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}